<template>
    <div>
        <h1>1111</h1>
        <!-- <van-uploader :after-read="afterRead"  v-model="fileList" :deletable=false :max-count="1" :name='11' /> -->
        <van-uploader :after-read="afterRead"  v-model="fileLists"  :max-count="1" />
        <van-uploader v-model="files" multiple :after-read="afterRead1" :max-count="1" class="sc" />
        <!-- <van-uploader icon="photo" :after-read="afterRead" />
          <van-uploader>
            <van-button icon="photo"  :max-count="1"  type="primary" :after-read="afterRead" >上传图片</van-button>
          </van-uploader>
          <van-uploader>
            <van-button icon="photo" :max-count="1"  v-model="fileLists" type="primary" :after-read="afterRead">上传图片</van-button>
          </van-uploader> -->
    </div>
</template>
<script>
    export default {
        data() {
            return {
                fileList: [],
                fileLists:[],
                files:[]
            }
        },
        methods: {
            afterRead(file) {
                // 此时可以自行将文件上传至服务器
                console.log(file);
                console.log("文件1")
            },
            afterRead1(file) {
                // 此时可以自行将文件上传至服务器
                console.log(file);
                console.log("文件2")
            }
        }
    }
</script>


